@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;800&display=swap");

:root {
  --color-primary: #585c6b;
  --color-secondary: #d3d1d1;
  --color-accent: #51e3a4;
  --color-accent-secondary: #87f2c4;
}

body {
  margin: 0;
  font-size: 1rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  color: var(--color-primary);
  background: #f0efe9;
}

#root {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main {
  background: #fff;
  width: 80%;
  max-width: 500px;
  margin: 0 auto;
  padding: 3em 2em 4em;
  position: relative;
}

.TodoItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

input[type="checkbox"] {
  display: none;
}

.fa-circle {
  font-size: 1.5rem;
  color: var(--color-secondary);
  transition: all 0.25s ease-in-out;
}

.fa-circle:hover {
  color: var(--color-accent);
}

.fa-check-circle {
  font-size: 1.5rem;
  color: var(--color-accent);
}

.fa-check-circle:hover {
  color: var(--color-accent-secondary);
}

.btn-primary {
  border: none;
  border-radius: 50%;
  padding: 0.2em 0.6em;
  cursor: pointer;
  font-size: 3rem;
  font-family: inherit;
  background-color: var(--color-accent);
  color: rgba(0, 0, 0, 0.2);
  position: absolute;
  /* top: 89%; */
  bottom: -8%;
  left: 50%;
  margin-left: -50px;
  transition: all 0.25s ease-in-out;
}

.btn-primary:hover,
.btn-primary:focus {
  background-color: var(--color-accent-secondary);
}

/* Date */

.date-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 3em;
  max-height: 50px;
}

.date {
  display: flex;
  align-items: center;
}

.date-number {
  font-size: 3.2rem;
  padding-right: 0.1em;
}

.date-month,
.date-year {
  margin: 0;
}

.date-year {
  color: var(--color-secondary);
}

.date-month,
.date-day {
  text-transform: uppercase;
}
